import { Fragment, useEffect } from "react";

import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Row, Col, Select } from "antd";
import {
  RiCheckboxCircleLine,
  RiAlertLine,
  RiInformationLine,
} from "react-icons/ri";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import {
  isModelVisible,
  InputChangeValue,
  getPurchaseById,
  stockOutAdd,
  getFormulaExecuter,
  getTotalRepairAmountByPurchase,
  getTotalReestimationAmountByPurchase,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

import {
  getMiniCounters,
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getAllCounters,
} from "../../../../../store/mini/miniSlice";

import { getSystemFormulas } from "../../../../../store/system/systemSlice";

const addStockOut = (props) => {
  const [form] = Form.useForm();
  const { Option } = Select;

  useEffect(() => {
    props.getSystemFormulas();
    props.getMiniCounters();
    props.getAllCounters();
    // props.getPurchaseAvailableStock({ ...props.purchaseAvailableStockParams });
  }, []);

  function vehicleDetails(vehicleId) {
    props.purchaseAvaliableStockList.forEach((purchase) => {
      if (purchase.vehicle.id == vehicleId) {
        props.getPurchaseById(purchase);
        props.getTotalRepairAmountByPurchase(purchase.id);
        props.getTotalReestimationAmountByPurchase(purchase.id);
      }
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      counter_id: props.stockOutData?.counter_id,
      tocounter_id: props.stockOutData?.tocounter_id,
      vehicle_id: props.stockOutData?.vehicle_id,
      purchase_id: props.stockOutData?.purchase_id,
      stock_purchase_id: props.stockOutData?.purchase_id,
      re_estimation_amount: props.stockOutData?.re_estimation_amount,
      repair_amount: props.stockOutData?.repair_amount,
      saleamount: props.stockOutData?.saleamount,
      profit: props.stockOutData?.profit,
    };
    props.stockOutAdd(obj);
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.stockOutData?.id,
      counter_id: props.stockOutData?.counter_id,
      tocounter_id: props.stockOutData?.tocounter_id,
      vehicle_id: props.stockOutData?.vehicle_id,
      purchase_id: props.stockOutData?.purchase_id,
      stock_purchase_id: props.stockOutData?.purchase_id,
      re_estimation_amount: props.stockOutData?.re_estimation_amount,
      repair_amount: props.stockOutData?.repair_amount,
      saleamount: props.stockOutData?.saleamount,
      profit: props.stockOutData?.profit,
    };
    props.stockOutAdd(obj);
  };
  function getMaxSaleAmount(data) {
    var variablesData = {
      PURCHASEAMOUNT:
        props.purchaseDetails.purchaserate == null
          ? 0
          : parseInt(props.purchaseDetails.purchaserate),
    };
    if (data.key == "purchaserate") {
      variablesData.PURCHASEAMOUNT = parseInt(
        data.value == "" ? 0 : data.value
      );
    }
    var maxSaleAmountObj = {
      code: "MAXSALEAMOUNT",
      variables: variablesData,
    };
    props.getFormulaExecuter(maxSaleAmountObj);
  }
  function getMinSaleAmount(data) {
    var variablesData = {
      PURCHASEAMOUNT:
        props.purchaseDetails.purchaserate == null
          ? 0
          : parseInt(props.purchaseDetails.purchaserate),
    };
    if (data.key == "purchaserate") {
      variablesData.PURCHASEAMOUNT = parseInt(
        data.value == "" ? 0 : data.value
      );
    }
    var minSaleAmountObj = {
      code: "MINSALEAMOUNT",
      variables: variablesData,
    };
    props.getFormulaExecuter(minSaleAmountObj);
  }
  function getMaxSaleAmountInMinSale(data) {
    var variablesData = {
      MINSALEAMOUNT:
        props.minSaleAmount == 0 ? 0 : parseInt(props.minSaleAmount),
    };
    if (data.key == "minSaleAmount") {
      variablesData.MINSALEAMOUNT = parseInt(data.value == "" ? 0 : data.value);
    }
    var minSaleAmountInMaxSaleObj = {
      code: "MAXSALEAMOUNT_MINISALEAMOUNT",
      variables: variablesData,
    };
    props.getFormulaExecuter(minSaleAmountInMaxSaleObj);
  }
  function getSaleProfit(data) {
    var variablesData = {
      SALEAMOUNT:
        props.stockOutData?.saleamount == null
          ? 0
          : parseInt(props.stockOutData?.saleamount),
      PURCHASEAMOUNT:
        props.purchaseDetails?.purchaserate == null
          ? 0
          : parseInt(props.purchaseDetails?.purchaserate),
      REPAIRAMOUNT:
        props.stockOutData?.repair_amount == null
          ? 0
          : parseInt(props.stockOutData?.repair_amount),
      REESTIMATIONAMOUNT:
        props.stockOutData?.re_estimation_amount == null
          ? 0
          : parseInt(props.stockOutData?.re_estimation_amount),
    };

    if (data.key == "saleamount") {
      variablesData.SALEAMOUNT = parseInt(data.value == "" ? 0 : data.value);
    } else if (data.key == "purchaserate") {
      variablesData.PURCHASEAMOUNT = parseInt(
        data.value == "" ? 0 : data.value
      );
    } else if (data.key == "repair_amount") {
      variablesData.REPAIRAMOUNT = parseInt(data.value == "" ? 0 : data.value);
    } else if (data.key == "re_estimation_amount") {
      variablesData.REESTIMATIONAMOUNT = parseInt(
        data.value == "" ? 0 : data.value
      );
    }
    var saleProfitObj = {
      code: "SALEPROFIT",
      variables: variablesData,
    };
    props.getFormulaExecuter(saleProfitObj);
  }

  function checkSaleAmount(data) {
    if (
      parseInt(data.value) >= props.minSaleAmount &&
      parseInt(data.value) <= props.maxSaleAmount
    ) {
      getSaleProfit(data);
      props.InputChangeValue(data);
      // getFinanceAmount(data);
    } else {
      props.InputChangeValue({ key: data.key, value: "" });
      Modal.info({
        icon: (
          <span className="remix-icon">
            <RiInformationLine />
          </span>
        ),
        title: (
          <h5 className="hp-mb-0 hp-font-weight-500">Sale Amount Error</h5>
        ),
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
              Please Enter Sale Amount Between {props.minSaleAmount} to{" "}
              {props.maxSaleAmount}
            </p>
          </div>
        ),
      });
    }
  }

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };
  function checkCounter() {
    if (props.stockOutData?.counter_id == props.stockOutData.tocounter_id) {
      Modal.error({
        icon: (
          <span className="remix-icon">
            <RiAlertLine />
          </span>
        ),
        title: <h5 className="hp-mb-0 hp-font-weight-500">Error</h5>,
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
              Sorry! Not allowd to same counter
            </p>
          </div>
        ),
      });
      props.InputChangeValue({
        key: "tocounter_id",
        value: null,
      });
    }
  }

  const FooterAddOrUpdate = () => {
    if (props.stockOutData.id == null || props.stockOutData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.stockOutData.id == null || props.stockOutData.id == 0
        ? "Add"
        : "Update"}{" "}
      Stock Out
    </h5>
  );

  // const onSearch = (value) => {
  //   console.log("search:", value);
  // };

  useEffect(() => {
    form.setFieldsValue({
      counter_id: props.stockOutData.counter_id,
      tocounter_id: props.stockOutData.tocounter_id,
      vehicle_id: props.stockOutData.vehicle_id,
      vehicleVersion: props.stockOutData.vehicleVersion,
      purchase: props.stockOutData.purchase,
      re_estimation_amount: props.stockOutData.re_estimation_amount,
      repair_amount: props.stockOutData.repair_amount,
      saleamount: props.stockOutData.saleamount,
      profit: props.stockOutData.profit,
    });
  }, [props.stockOutData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.stockOutData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row>
          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="From Counter"
              name="counter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select From Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select From Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onBlur={(value) => {
                  props.getPurchaseAvailableStock({
                    ...props.purchaseAvailableStockParams,
                    counter: props.stockOutData?.counter_id,
                    page: 1,
                  });
                  props.setPurchaseAvailableStockParams({
                    ...props.purchaseAvailableStockParams,
                    counter: props.stockOutData?.counter_id,
                    page: 1,
                  });
                }}
                onChange={(value) => {
                  // props.GetCounterByID(value);
                  props.getPurchaseAvailableStock();
                  props.InputChangeValue({
                    key: "counter_id",
                    value: value,
                  });
                }}
                value={
                  props.stockOutData.counter_id == null
                    ? ""
                    : props.stockOutData?.counter_id
                }
              >
                {props.miniCountersList.map((counter, index) => {
                  return (
                    <Option key={index} value={counter.id}>
                      {counter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="To Counter"
              name="tocounter_id"
              rules={[
                {
                  required: true,
                  message: "Please Select To Counter",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select To Counter"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onBlur={() => {
                  checkCounter();
                }}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "tocounter_id",
                    value: value,
                  });
                }}
                value={
                  props.stockOutData.tocounter_id == null
                    ? ""
                    : props.stockOutData?.tocounter_id
                }
              >
                {props.allCountersList.map((tocounter, index) => {
                  return (
                    <Option key={index} value={tocounter.id}>
                      {tocounter.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Vehicle Number"
              name="vehicle_id"
              rules={[
                {
                  required: true,
                  message: "Please Input Vehicle Number",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select vehicle "
                optionFilterProp="vehicle"
                filterOption={false}
                onSearch={(value) => {
                  props.setPurchaseAvailableStockParams({
                    ...props.purchaseAvailableStockParams,
                    page: 1,
                    search: value,
                  });
                  props.getPurchaseAvailableStock({
                    ...props.purchaseAvailableStockParams,
                    page: 1,
                    search: value,
                  });
                }}
                onBlur={(value) => {
                  if (props.purchaseDetails?.purchaserate != null) {
                    if (props.minSaleAmount == 0) {
                      getMaxSaleAmount({
                        key: "purchaserate",
                        value: props.purchaseDetails?.purchaserate,
                      });
                      getMinSaleAmount({
                        key: "purchaserate",
                        value: props.purchaseDetails?.purchaserate,
                      });
                    } else {
                      getMaxSaleAmountInMinSale({
                        key: "minSaleAmount",
                        value: props.minSaleAmount,
                      });
                    }
                  }
                }}
                onChange={(value) => {
                  vehicleDetails(value);
                  props.InputChangeValue({
                    key: "vehicle_id",
                    value: value,
                  });
                }}
                onPopupScroll={() => {
                  if (
                    props.purchaseAvailableStockParams.page <
                    props.purchaseAvailableStockParams.no_of_pages
                  ) {
                    props.setPurchaseAvailableStockParams({
                      ...props.purchaseAvailableStockParams,
                      page: props.purchaseAvailableStockParams.page + 1,
                    });
                    props.getPurchaseAvailableStock({
                      ...props.purchaseAvailableStockParams,
                      page: props.purchaseAvailableStockParams.page + 1,
                    });
                  }
                }}
                value={
                  props.stockOutData.vehicle_id == null
                    ? ""
                    : props.stockOutData?.vehicle_id
                }
              >
                {props.purchaseAvaliableStockList.map((purchase, index) => {
                  return (
                    <Option key={index} value={purchase?.vehicle?.id}>
                      {purchase?.vehicle.vehicleno} - {purchase?.code}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Vehicle Info">
              <Input
                id="vehicleVersion"
                name="vehicleVersion"
                style={{ width: "100%" }}
                placeholder="Vehicle Info"
                value={props.purchaseDetails?.vehicle?.version?.name}
                disabled
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Purchase Amount">
              <Input
                id="purchase"
                name="purchase"
                style={{ width: "100%" }}
                placeholder="Purchase Amount"
                value={props.purchaseDetails?.purchaserate}
                disabled
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="RE Amount"
              name="re_estimation_amount"
              rules={[
                {
                  required: true,
                  message: "Please Input RE Amount",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="re_estimation_amount"
                name="re_estimation_amount"
                style={{ width: "100%" }}
                maxLength={6}
                placeholder="RE Amount"
                disabled
                value={
                  props.stockOutData.re_estimation_amount == null
                    ? ""
                    : props.stockOutData?.re_estimation_amount
                }
                onBlur={(value) => {
                  getSaleProfit(value);
                }}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Repair Amount"
              name="repair_amount"
              rules={[
                {
                  required: true,
                  message: "Please Input Repair Amount",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="repair_amount"
                name="repair_amount"
                maxLength={6}
                style={{ width: "100%" }}
                placeholder="Repair Amount"
                disabled
                value={
                  props.stockOutData.repair_amount == null
                    ? ""
                    : props.stockOutData?.repair_amount
                }
                onBlur={(value) => {
                  getSaleProfit(value);
                }}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Sale Amount"
              name="saleamount"
              rules={[
                {
                  required: true,
                  message: "Please Input Sale Amount",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="saleamount"
                name="saleamount"
                style={{ width: "100%" }}
                placeholder="Sale Amount"
                maxLength={6}
                value={
                  props.stockOutData.saleamount == null
                    ? ""
                    : props.stockOutData?.saleamount
                }
                onBlur={(value) => {
                  checkSaleAmount(value);
                }}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Profit"
              name="profit"
              rules={[
                {
                  required: true,
                  message: "Please Input Profit",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="profit"
                name="profit"
                style={{ width: "100%" }}
                placeholder="Profit"
                disabled
                value={
                  props.stockOutData.profit == null
                    ? ""
                    : props.stockOutData?.profit
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ stockRequest, mini }) {
  return {
    stockOutData: stockRequest.stockOutData,
    model: stockRequest.model,
    purchaseDetails: stockRequest.purchaseDetails,
    minSaleAmount: stockRequest.minSaleAmount,
    maxSaleAmount: stockRequest.maxSaleAmount,
    miniCountersList: mini.miniCountersList,
    allCountersList: mini.allCountersList,
    purchaseAvaliableStockList: mini.purchaseAvaliableStockList,
    purchaseAvailableStockParams: mini.purchaseAvailableStockParams,
  };
}

export default connect(mapStateToProps, {
  isModelVisible,
  InputChangeValue,
  getMiniCounters,
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getPurchaseById,
  stockOutAdd,
  getAllCounters,
  getFormulaExecuter,
  getTotalRepairAmountByPurchase,
  getTotalReestimationAmountByPurchase,
  getSystemFormulas,
})(addStockOut);
