import React from "react";

import { Card, Row, Col, Table, Space, Button, Typography } from "antd";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  getStockTransferViewById,
  isStockTransferModelVisible,
  stockTransferAccept,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

function StockRequestListView(props) {
  const { Text, Link } = Typography;

  const { stockTransferList, stockType, value } = props;
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%"
    },
    // {
    //   title: "Date",
    //   width: "5%",
    //   render: (row) => <span className={columnClass}>{row.date}</span>,
    // },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: "10%",
    },

    {
      title: "Vehicle No",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {row.stock_purchase?.vehicle?.vehicleno}
        </span>
      ),
    },
    {
      title: "To Counter",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>{row.tocounter?.name}</span>
      ),
    },
    {
      title: "From Counter",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.counter.name}</span>,
    },
    {
      title: "Approval Status",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {
            <Text
              type={
                row.transferstatus == 2 || row.transferstatus == 4
                  ? "success"
                  : row.transferstatus == 3 || row.transferstatus == 5
                  ? "danger"
                  : "warning"
              }
            >
              {row.transferstatus_name == null
                ? "Pending"
                : row.transferstatus_name}
            </Text>
          }
        </span>
      ),
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          <Button
            block
            onClick={() => {
              console.log("row", row);
              props.isStockTransferModelVisible(true);
              props.getStockTransferViewById({ value: row.id, stockType });
            }}
            className="hp-px-12 hp-btn-bg-color"
          >
            View
          </Button>
        </Space>
      ),
    },
  ];

  function deleteRecord(record) {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        props.stockOutDelete(record.id);
      }
    });
  }

  const data = stockTransferList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index: (props.stockRequestparams.page - 1) * props.stockRequestparams.page_size + (key + 1),
  }));

  return (
    <Card>
      <Row className="hp-mb-12">
        <Col span={24}>
          <Table
            columns={columns}
            rowClassName="hp-btn-bg-color-none"
            dataSource={data}
            pagination={false}
            size="middle"
            scroll={{ x: 500 }}
          />
        </Col>
      </Row>
    </Card>
  );
}

function mapStateToProps({ stockRequest }) {
  return {
    noofpages: stockRequest.noofpages,
    listCount: stockRequest.listCount,
    RejectMode: stockRequest.RejectMode,
    stockRequestparams: stockRequest.stockRequestparams,
  };
}

export default connect(mapStateToProps, {
  isStockTransferModelVisible,
  getStockTransferViewById,
  stockTransferAccept,
})(StockRequestListView);
