import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isRequestDrawerVisible,
  RequestFilterInputChangeValue,
  getStockRequestList,
  setStockRequestParams,
  getStockAcceptList,
} from "../../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

import {
  getMiniCounters,
  getAllCounters,
} from "../../../../../../store/mini/miniSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniCounters();
    props.getAllCounters();
  }, []);

  const onFinish = () => {
    if (props.activeTab == 1) {
      props.getStockRequestList({
        ...props.stockRequestparams,
        start_date:
          props.stockRequestparams.start_date != null &&
          props.stockRequestparams.start_date != ""
            ? moment(props.stockRequestparams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockRequestparams.end_date != null &&
          props.stockRequestparams.end_date != ""
            ? moment(props.stockRequestparams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
        page: 1,
        search: "",
        page_size: 10,
      });
    } else {
      props.getStockAcceptList({
        ...props.stockRequestparams,
        start_date:
          props.stockRequestparams.start_date != null &&
          props.stockRequestparams.start_date != ""
            ? moment(props.stockRequestparams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockRequestparams.end_date != null &&
          props.stockRequestparams.end_date != ""
            ? moment(props.stockRequestparams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
        page: 1,
        search: "",
        page_size: 10,
      });
    }
    props.setStockRequestParams({
      ...props.stockRequestparams,
      start_date: props.stockRequestparams.start_date,
      end_date: props.stockRequestparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.isRequestDrawerVisible(false);
  };

  function clearFilter() {
    props.setStockRequestParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getStockRequestList({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  const transferStatusList = [
    { id: 1, name: "Pending" },
    { id: 2, name: "Approved" },
    { id: 3, name: "Rejected" },
    { id: 4, name: "Accepted" },
    { id: 5, name: "To Counter Rejected" },
  ];

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.stockRequestparams?.start_date,
      end_date: props.stockRequestparams?.end_date,
      counter: props.stockRequestparams.counter,
      tocounter: props.stockRequestparams.tocounter,
      transferstatus: props.stockRequestparams.transferstatus
    });
  }, [props.stockRequestparams]);

  function onSearch(val) {
    props.getStockRequestList({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          onClose={() => {
            props.isRequestDrawerVisible(false);
          }}
          placement="right"
          visible={props.stockRequestDrawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.RequestFilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.RequestFilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.stockRequestparams?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.stockRequestparams.start_date == null ||
                      props.stockRequestparams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.RequestFilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="From Counter" name="counter">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select From Counter"
                    optionFilterProp="Counter"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.RequestFilterInputChangeValue({
                        key: "counter",
                        value: value,
                      });
                    }}
                    value={
                      props.stockRequestparams.counter_id == null
                        ? ""
                        : props.stockRequestparams?.counter_id
                    }
                  >
                    {props.miniCountersList.map((counter, index) => {
                      return (
                        <Option key={index} value={counter.id}>
                          {counter.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Counter" name="tocounter">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select To Counter"
                    optionFilterProp="Counter"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.RequestFilterInputChangeValue({
                        key: "tocounter",
                        value: value,
                      });
                    }}
                    value={
                      props.stockRequestparams.tocounter_id == null
                        ? ""
                        : props.stockRequestparams?.tocounter_id
                    }
                  >
                    {props.allCountersList.map((tocounter, index) => {
                      return (
                        <Option key={index} value={tocounter.id}>
                          {tocounter.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Transfer Status" name="transferstatus">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Transfer Status"
                    optionFilterProp="transferstatus"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.RequestFilterInputChangeValue({
                        key: "transferstatus",
                        value: value,
                      });
                    }}
                    value={
                      props.stockRequestparams.transferstatus == null
                        ? ""
                        : props.stockRequestparams?.transferstatus
                    }
                  >
                    {transferStatusList.map((transfer, index) => {
                      return (
                        <Option value={transfer.id}>{transfer.name}</Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ stockRequest, mini }) {
  return {
    stockRequestDrawer: stockRequest.stockRequestDrawer,
    stockRequestparams: stockRequest.stockRequestparams,
    miniCountersList: mini.miniCountersList,
    allCountersList: mini.allCountersList,
    activeTab: stockRequest.activeTab,
  };
}

export default connect(mapStateToProps, {
  getStockRequestList,
  isRequestDrawerVisible,
  setStockRequestParams,
  RequestFilterInputChangeValue,
  getMiniCounters,
  getAllCounters,
  getStockAcceptList,
})(filter);
