import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Tabs,
  Spin,
} from "antd";
import { Search } from "react-iconly";

import {
  FilterOutlined,
  UploadOutlined,
  PlusOutlined,
  OrderedListOutlined,
  AppstoreOutlined,
} from "@ant-design/icons";
import StockRequestListView from "./StockRequestListView";
import StockRequestGridView from "./StockRequestGridView";
import { isMobile } from "react-device-detect";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import { getReportList } from "../../../../../store/reports/reportsSlice";
import {
  getStockRequestList,
  getStockAcceptList,
  setStockRequestParams,
  isModelVisible,
  isRequestDrawerVisible,
  stockTransferActiveTab
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import AddStockOut from "./addStockOut";
import StockTransferView from "./stockTransferView";
import Filter from "./Filters/Filter";

function StockRequest(props) {
  const { Search } = Input;
  const { TabPane } = Tabs;
  useEffect(() => {
    props.getStockRequestList({
      ...props.stockRequestparams,
      page: 1,
      search: "",
      page_size: 10,
    });


  }, []);

  const [productListToggle, setProductListToggle] = useState(false);

  function onPageChange(page, pageSize) {
    if (props.activeTab == 1) {
      props.getStockRequestList({
        ...props.stockRequestparams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    } else {
      props.getStockAcceptList({
        ...props.stockRequestparams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    }
    props.setStockRequestParams({
      ...props.stockRequestparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }


  function onSearch(val) {
    if (props.activeTab == 1) {
      props.getStockRequestList({ ...props.stockRequestparams, page: 1, search: val });
    } else {
      props.getStockAcceptList({ ...props.stockRequestparams, page: 1, search: val });
    }
    props.setStockRequestParams({ ...props.stockRequestparams, page: 1, search: val });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const onChange = (key) => {
    props.stockTransferActiveTab(key);
    if (key == 1) {
      props.getStockRequestList({
        page: 1,
        search: "",
        page_size: 10,
      });
    } else {
      props.getStockAcceptList({
        page: 1,
        search: "",
        page_size: 10,
      });
    }
    props.setStockRequestParams({
      saletype: key,
      page: 1,
      search: "",
      page_size: 10,
    });
  };

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      // model_name: "StockTransferIn",
    };

    if(props.activeTab == 1) {
      objData.model_name = "StockTransferOut"
    }
    else{
      objData.model_name = "StockTransferIn"
    }
      
   
    props.getReportList({
      objData,
      ParamsData: {
        ...props.stockRequestparams,
        start_date:
          props.stockRequestparams.start_date != null &&
          props.stockRequestparams.start_date != ""
            ? moment(props.stockRequestparams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockRequestparams.end_date != null &&
          props.stockRequestparams.end_date != ""
            ? moment(props.stockRequestparams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Stock Transfer" goBack={false} />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col span={24} xs={24} sm={24} md={props.activeTab == 1 ? 12 : 15}
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              value={
                props.stockRequestparams.search == "" ||
                props.stockRequestparams.search == null
                  ? ""
                  : props.stockRequestparams.search
              }
              onChange={(e) => {
                if (e.target.value == "") {
                  if (props.activeTab == 1) {
                    props.getStockRequestList({
                      ...props.stockRequestparams,
                      page: 1,
                      search: "",
                    });
                  } else  {
                    props.getStockAcceptList({
                      ...props.stockRequestparams,
                      page: 1,
                      search: "",
                    });
                  }
                  props.setStockRequestParams({
                    ...props.stockRequestparams,
                    page: 1,
                    search: "",
                  });
                }
                else {
                  props.setStockRequestParams({
                    ...props.stockRequestparams,
                    search: e.target.value,
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          {props.activeTab == 1 && (
            <>
              {props.userAccessList?.indexOf("StockTransfer.add_stocktransfer") !== -1 && (
                <Col span={24} xs={4} sm={4} md={4}>
                  <Button
                    onClick={() => {
                      props.isModelVisible(true);
                    }}
                    className="hp-w-100 hp-p-10"
                    icon={<PlusOutlined className="hp-mr-10" />}
                    
                  >
                    {!isMobile && "Add Stock Out" }
                  </Button>
                </Col>
              )}
            </>
          )}
          <Col span={24} xs={4} sm={4}  md={3}>
            <Button
              onClick={() => {
                props.isRequestDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-10"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf("StockTransfer.export_stocktransfer") !== -1 && (
            <Col span={24} xs={4} sm={4}  md={3}>
              <Button
                onClick={() => {
                  exportData();
                }}
                className="hp-w-100 hp-p-10 "
                icon={<UploadOutlined className="hp-mr-10" />}
                
              >
                 {!isMobile && "Export"}
              </Button>
            </Col>
          )}
          
          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-10"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-10"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Card>

          <Tabs defaultActiveKey={1} type="card" onChange={onChange}>
            <TabPane tab="Stock Out" key="1">
              <Spin tip="loading" spinning={props.loading} delay={500}>
                {props.stockRequestList.length !== 0 ? (
                  <>
                    {!productListToggle ? (
                      <>

                        <List
                          dataSource={props.stockRequestList}
                          renderItem={(value) => (
                            <StockRequestGridView
                              value={value}
                              stockType={props.activeTab}
                            />
                          )}
                        />
                        <Pagination
                          size="small"
                          total={props.stockRequestlistCount}
                          showTotal={showTotal}
                          showSizeChanger
                          current={props.stockRequestparams?.page}
                           pageSize={props.stockRequestparams?.page_size}
                          onChange={onPageChange}
                          className="hp-mb-12 hp-mt-24 hp-float-right"
                        />
                      </>
                    ) : (
                      <>
                        <StockRequestListView
                          stockTransferList={props.stockRequestList}
                          stockType={props.activeTab}
                        />{" "}
                        <Pagination
                          size="small"
                          total={props.stockRequestlistCount}
                          showTotal={showTotal}
                          showSizeChanger
                          current={props.stockRequestparams?.page}
                           pageSize={props.stockRequestparams?.page_size}
                          onChange={onPageChange}
                          className="hp-mb-12 hp-mt-24 hp-float-right"
                        />
                      </>
                    )}
                  </>
                ) : (
                  <Card>
                    <Empty
                      className="hp-m-32"
                      image={EmptyImage}
                      imageStyle={{
                        height: 160,
                      }}
                      description={<h5>Your Stock Request list is empty</h5>}
                    ></Empty>
                  </Card>
                )}
              </Spin>
            </TabPane>

            <TabPane tab="Stock In" key="2">
              <Spin tip="loading" spinning={props.loading} delay={500}>
                {props.stockAcceptList.length !== 0 ? (
                  <>
                    {!productListToggle ? (
                      <>
                        <List
                          dataSource={props.stockAcceptList}
                          renderItem={(value) => (
                            <StockRequestGridView
                              value={value}
                              stockType={props.activeTab}
                            />
                          )}
                        />
                        <Pagination
                          size="small"
                          total={props.listCount}
                          showTotal={showTotal}
                          current={props.stockRequestparams?.page}
                           pageSize={props.stockRequestparams?.page_size}
                          showSizeChanger
                          onChange={onPageChange}
                          className="hp-mb-12 hp-mt-24 hp-float-right"
                        />
                      </>
                    ) : (
                      <>
                        <StockRequestListView
                          stockTransferList={props.stockAcceptList}
                          stockType={props.activeTab}
                        />{" "}
                        <Pagination
                          size="small"
                          total={props.listCount}
                          showTotal={showTotal}
                          current={props.stockRequestparams?.page}
                          pageSize={props.stockRequestparams?.page_size}
                          showSizeChanger
                          onChange={onPageChange}
                          className="hp-mb-12 hp-mt-24 hp-float-right"
                        />
                      </>
                    )}
                  </>
                ) : (
                  <Card>
                    <Empty
                      className="hp-m-32"
                      image={EmptyImage}
                      imageStyle={{
                        height: 160,
                      }}
                      description={<h5>Your Stock Accept list is empty</h5>}
                    ></Empty>
                  </Card>
                )}
              </Spin>
            </TabPane>
          </Tabs>

        </Card>
      </Col>
      <AddStockOut />
      <StockTransferView />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ stockRequest, system }) {
  return {
    stockRequestList: stockRequest.stockRequestList,
    stockAcceptList: stockRequest.stockAcceptList,
    noofpages: stockRequest.noofpages,
    listCount: stockRequest.listCount,
    stockRequestlistCount: stockRequest.stockRequestlistCount,
    stockRequestparams: stockRequest.stockRequestparams,
    loading: stockRequest.loading,
    activeTab: stockRequest.activeTab,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getStockRequestList,
  getStockAcceptList,
  setStockRequestParams,
  isRequestDrawerVisible,
  isModelVisible,
  stockTransferActiveTab,
  getReportList
})(StockRequest);
